@import "../../colors";

#body-wrapper {
  margin: 40px auto;
  & .section-container {
    padding: 0 10vw;
  }

  flex: 1 0 auto;
  width: 100%;
  & a {
    color: $primary-purple;
    &:active {
      color: $primary-purple-m-40 !important;
    }
    &:hover {
      color: $primary-purple-m-20;
    }
  }

  & h2 {
    font-family: "Saira Extra Condensed", sans-serif;
    font-size: 64px;
    font-weight: 600;
    letter-spacing: 5px;
    line-height: 150%;
    text-align: center;
    text-transform: uppercase;

    display: flex;
    align-items: center;
    flex-direction: column;
    &:after {
      content: "";
      text-align: center;
      display: block;
      width: 80px;
      height: 8px;
      background-color: $primary-purple;
    }
  }

  & p {
    margin: 0 10vw 16px;
    @media (max-width: 768px) {
      margin: 0 0 16px;
    }
    font-size: 20px;
    line-height: 140%;
  }
}
